/// <reference types="@cloudflare/workers-types" />

import { CodexEngine } from "../src/codex/engine";

const JSON_HEADERS = {
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
};

function json(body: unknown, init?: ResponseInit) {
    return new Response(JSON.stringify(body, null, 2), {
        headers: JSON_HEADERS,
        ...init,
    });
}

async function requireToken(request: Request, env: OmniEnv) {
    if (!env.OMNI_ACCESS_TOKEN) {
        return;
    }

    const token = request.headers.get("x-omni-key");
    if (token !== env.OMNI_ACCESS_TOKEN) {
        throw new Response(JSON.stringify({ error: "Unauthorized" }), {
            status: 401,
            headers: JSON_HEADERS,
        });
    }
}

async function pullCodexContext(env: OmniEnv): Promise<string> {
    if (env.CODEX_API) {
        try {
            const res = await fetch(`${env.CODEX_API.replace(/\/$/, "")}/entries`);
            if (res.ok) {
                const payload = (await res.json()) as { entries?: Array<{ title: string; synopsis: string }> };
                return (payload.entries ?? [])
                    .slice(0, 5)
                    .map((entry) => `- ${entry.title}: ${entry.synopsis}`)
                    .join("\n");
            }
        } catch (err) {
            console.warn("Codex API fetch failed", err);
        }
    }

    return CodexEngine.list()
        .slice(0, 5)
        .map((entry) => `- ${entry.title}: ${entry.synopsis}`)
        .join("\n");
}

async function handleChat(request: Request, env: OmniEnv) {
    await requireToken(request, env);
    const body = await request.json();
    const messages = Array.isArray(body.messages) ? body.messages : [];

    const codexContext = await pullCodexContext(env);
    const systemMessage = {
        role: "system",
        content: `You are Omni, the MKP TRI operations AI. Reference the following Codex context when responding:\n${codexContext}`,
    };

    const model = env.OMNI_MODEL ?? "@cf/meta/llama-3-8b-instruct";
    const response = await env.AI.run(model, {
        messages: [systemMessage, ...messages],
        max_output_tokens: 600,
    });

    return json({ model, response });
}

function handleOptions() {
    return new Response(null, {
        headers: {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Methods": "POST,GET,OPTIONS",
            "Access-Control-Allow-Headers": "Content-Type,X-Omni-Key",
        },
    });
}

interface OmniEnv {
    AI: Ai;
    OMNI_MODEL?: string;
    CODEX_API?: string;
    OMNI_ACCESS_TOKEN?: string;
}

export default {
    async fetch(request: Request, env: OmniEnv): Promise<Response> {
        const url = new URL(request.url);

        if (request.method === "OPTIONS") {
            return handleOptions();
        }

        if (url.pathname === "/health") {
            return json({ ok: true, model: env.OMNI_MODEL ?? "default", timestamp: new Date().toISOString() });
        }

        if (url.pathname === "/chat" && request.method === "POST") {
            try {
                return await handleChat(request, env);
            } catch (err) {
                if (err instanceof Response) {
                    return err;
                }
                console.error("Omni proxy error", err);
                return json({ error: "Internal error" }, { status: 500 });
            }
        }

        return json({ message: "Omni LLM proxy ready", routes: ["POST /chat", "GET /health"] });
    },
};
